.imageCard{
    width: calc((100% / 3) - 10px);
    margin: 0px 5px 5px 5px;
    max-width: inherit !important ;
}

@media screen and (max-width: 991px) {
    .imageCard{
        width: calc((100% / 2) - 10px);
    }
}
@media screen and (max-width: 767px) {
    .imageCard{
        width: 100%;
    }
}

.imageCard .MuiCardMedia-root:hover {
    cursor: pointer;
}

